html {
  font-size: 16px;
}

body {
  font-family: "Roboto", "Noto Sans KR";
  max-width: 1920px;
}

a {
  text-decoration: none;
}

p {
  margin-bottom: 0;
}

.App .navbar-expand {
  padding: 0px;
}

.App .navbar {
  background-color: #fff;
  padding: 0 70px;
}

.App .navbar .container {
  font-weight: 300;
  border-bottom: 1px solid #111;
  padding: 10px 0 10px 0;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-bottom: 20px;
}

.App .navbar .container .msAuto {
  font-size: 17px;
  font-weight: 500;
  border: 1.5px solid #bbb;
  border-radius: 20px;
  padding: 7px 20px;
}

.App .navbar .container .navbar-brand {
  letter-spacing: 0.8px;
  font-weight: 400;
}

.App .navbar .container .linkbar-brand {
  display: block;
  padding: 10px;
  font-size: var(--bs-nav-link-font-size);
  font-weight: var(--bs-nav-link-font-weight);
  color: var(--bs-nav-link-color);
  text-decoration: none;
  -webkit-transition: 0.15s ease-in-out;
  transition: 0.15s ease-in-out;
}

.App .navbar .container .linkbar-brand:hover {
  color: #111;
}

.App .nav-tabs {
  margin: 20px 0;
}

.App .main-bg {
  height: 350px;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-bottom: 1px solid #ccc;
}

.App .cartBox {
  position: fixed;
  border-radius: 5px;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  border: 1px solid #ccc;
  width: 150px;
  left: 83%;
  top: 20%;
}

.App .cartBox .watchedItem {
  margin: 15px;
}

.App .cartBox .watchedItem .localImg {
  width: 120px;
  height: 100px;
  border-radius: 10px;
}

.App .container02 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.App .container02 .row {
  padding: 10px;
  width: 80%;
}

.App .container02 .row .col-md-4 {
  margin-top: 20px;
}

.App .container02 .row .col-md-4 img {
  width: clamp(180px, 17vw, 400px);
  height: clamp(120px, 23vh, 200px);
  margin: 0 auto;
  border-radius: 20px;
}

.App .container02 .row .col-md-4 h4 {
  margin-top: 10px;
  font-weight: 300;
  font-size: 18px;
}

.App .container02 .row .col-md-4 p {
  margin-bottom: 5px;
}

.App .plusBtn {
  width: 120px;
  height: 40px;
  color: #fff;
  margin: auto 0;
  font-size: 15px;
  border-radius: 5px;
  border: 2px solid #ccc;
  background-color: cornflowerblue;
}

.App .plusBtn:hover {
  font-weight: 700;
  color: cornflowerblue;
  background-color: white;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.App .start {
  opacity: 0;
  width: 1280px;
}

.App .end {
  opacity: 1;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.App .min-w-full .bg-white .qaDetail {
  display: -webkit-box;
  height: 2.6em;
  overflow: hidden;
  line-height: 2.6;
  -webkit-line-clamp: 1;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  word-break: break-all;
  height: 4.5rem;
}

.App .max-w-screen-lg {
  max-width: 1280px;
}

.App .max-w-screen-lg .text-xl {
  font-size: 26px;
  margin-bottom: 10px;
}

.App .max-w-screen-lg .text-xl .min-w-full .text-gray-300 {
  font-size: 18px;
}

.App .max-w-screen-lg .h-screen .shadow-lg form .p-3 {
  font-weight: 500;
  font-size: 18px;
}

.App .max-w-screen-lg .h-screen .shadow-lg form .p-3 .title {
  margin: auto 20px;
}

.App .max-w-screen-lg .h-screen .shadow-lg form .p-3 .border-gray-100 {
  height: 50px;
  width: 70%;
  padding-left: 10px;
  margin: 10px 0;
  border-radius: 10px;
  overflow: auto;
}

.App .max-w-screen-lg .h-screen .shadow-lg form .p-3 .backform {
  min-height: 150px;
}

.App .max-w-screen-lg .h-screen .shadow-lg form .p-3 .qaBackform {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.App .coupon01 {
  margin: 30px auto;
}

.App .eventTitle01 {
  font-size: 32px;
  margin: 30px;
}

.App .eventTitle02 {
  font-size: 24px;
  font-weight: 500;
  padding-bottom: 10px;
  border-bottom: 3px solid #111;
}

.App .cartTitle {
  font-size: 26px;
  font-weight: 500;
  margin: 20px 0 40px 0;
}

.App .review .reviewImages {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 2rem;
  margin: 100px auto;
  width: 60%;
}

.App .review .reviewImage {
  width: 30%;
  height: 30%;
  color: #fff;
  border-radius: 10px;
  -webkit-box-shadow: 3px 3px 3px #ccc;
          box-shadow: 3px 3px 3px #ccc;
  overflow: hidden;
}

.App .review img {
  display: block;
  width: 100%;
}

.App .review .pagenation {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 5rem;
  font-size: 1.2rem;
  gap: 5px;
}

.App .review .pagenation .active {
  border: 3px solid #6e6e6e;
}

.App .review .pagenation .page-num {
  padding: 8px 15px;
  cursor: pointer;
  border-radius: 3px;
  font-weight: 400;
}

.App .review .pagenation .page-num:hover {
  border: 3px solid #6e6e6e;
  color: #111111;
  -webkit-transition: 0.25s all ease-in-out;
  transition: 0.25s all ease-in-out;
}

.App .review .reviewTitle {
  font-size: 19px;
  color: #111;
  padding: 10px;
}

.App .loading h3 {
  font-size: 24px;
  margin-bottom: 15px;
}

.App .loading h5 {
  font-size: 18px;
  font-weight: 300;
  color: #999999;
}

.App .searchBox {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}

.App .searchBox .search {
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  display: block;
  margin: 0px 0px 40px 0;
  width: 220px;
  height: 50px;
  border-bottom: 3px solid black;
  border-top: none;
  border-left: none;
  border-right: none;
}

.start {
  opacity: 0;
}

.end {
  opacity: 1;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.alertNone {
  display: none;
}

.table {
  width: 1280px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 0 auto;
}

.table thead {
  border-bottom: solid 1px #ccc;
}

.table td {
  font-size: 22px;
}

.table td span {
  padding: 2.5px 15px;
}

.table td .delete {
  color: #786a6d;
}

.detail {
  padding-top: clamp(50px, 10vh, 100px);
}

.detail .material-icons {
  font-size: 40px;
}

.detail .material-icons:hover {
  color: #d22340;
  font-size: 40px;
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

.detail .heart {
  color: #d22340;
}

.detail .pt-5 {
  font-size: 32px;
  padding: 15px;
  padding-top: 1.5rem !important;
}

.detail p {
  font-weight: 300;
  color: #565656;
}

.detail .price {
  font-weight: 500;
}

.detail .btn-danger {
  margin: 20px 8px 8px 8px;
}

.board {
  width: 1080px;
  margin: 100px auto;
}

.board .boardTitle {
  margin: 0 0 30px 0;
}

.board .boardTitle h4 strong {
  font-size: 2rem;
}

.board .boardTitle p {
  margin: 5px;
  font-size: 1rem;
}

.board .btWrap {
  margin: 30px;
  text-align: center;
  font-size: 0;
}

.board .btWrap .on {
  background: cornflowerblue;
  color: #fff;
}

.board .btWrap button {
  margin: 0 0 0 10px;
  display: inline-block;
  min-width: 10px;
  border: 1px solid cornflowerblue;
  border-radius: 2px;
  font-size: 1.4rem;
  padding: 0px 10px;
}

.board .btWrap button:first-child {
  margin: 0;
}

.post form {
  margin-top: 50px;
}

.post form .question {
  height: 150px;
}

.post form .text-gray-800 {
  padding-left: 5px;
  font-size: 15px;
}

.post form .w-full {
  font-size: 15px;
  margin-bottom: 10px;
}

@media all and (max-width: 960px) {
  .App .detail {
    padding-top: 20px;
  }
}

@media all and (max-width: 767px) {
  .App .container02 .row .col-md-4 img {
    width: clamp(320px, 17vw, 400px);
    height: clamp(200px, 23vh, 250px);
  }
  .App .container02 .row .col-md-4 h4 {
    margin-top: 10px;
    font-weight: 300;
    font-size: 16px;
  }
  .App .container02 .row .col-md-4 p {
    font-style: italic;
    font-weight: 400;
  }
  .App .detail .pt-5 {
    font-size: 24px;
  }
}
